import React from "react";
import ReactExport from "react-data-export";

const ExcelFile = ReactExport.ExcelFile;
const ExcelSheet = ReactExport.ExcelFile.ExcelSheet;
const ExcelColumn = ReactExport.ExcelFile.ExcelColumn;

class ExcelTable extends React.Component {
    render() {
        if (this.props.data === undefined || this.props.data.length === 0) {
            return null;
        }

        return (
            <ExcelFile element={this.props.button} filename={"NTCL-MonitoringData"}>
                {this.props.data.map((data, index) => {
                    return (
                        <ExcelSheet key={index} data={data} name={this.props.tanksNames[index]}>
                            <ExcelColumn label="Time" value="time" />
                            <ExcelColumn label="Left In Tank" value="level" />
                            <ExcelColumn label="Volume" value="vol" />
                            <ExcelColumn label="Readings" value="samples" />
                        </ExcelSheet>
                    );
                })}
            </ExcelFile>
        );
    }
}

export default ExcelTable;